import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhEcommerce, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhType } from 'src/app/sales/interface/vh-type';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-by-product-app',
  templateUrl: './by-product-app.component.html',
  styleUrls: ['./by-product-app.component.scss']
})
export class ByProductAppComponent implements OnInit {

  listProduct: Array<any> = []; //danh sách product theo từng id_shop
  listSubProduct: Array<any> = []; //danh sách product theo từng id_shop
  listProductApp: Array<any> = []; //sp của app chứa tiktok[]
  listSubProductApp: Array<any> = []; //sp của app chứa tiktok[]
  /**----------------------------------- */
  infoShop: any = {};
  searchValue: any = '';
  heightScroll: any;
  setHeight = { "max-height": "500px", "height": "500px" };
  loading: boolean = false;
  id_branch: string = this.vhQuerySales.getDefaultBranch()._id;
  constructor(
    private router: Router,
    public lang: LanguageService,
    public vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    public vhQuerySales: VhQuerySales,
    private cdRef: ChangeDetectorRef,
    private vhEcommerce: VhEcommerce
  ) {
  }

  ngOnInit(): void {
    this.getData();
  }
  getData() {
    this.infoShop = this.vhEcommerce.getlocalShopListTiktok()[0];
    this.changeShop(this.infoShop._id);
  }
  /**Chọn shop và lọc sản phẩm theo shop đó */
  changeShop(id) {
    let localProducts: Array<any> = this.vhQuerySales.getlocalDetailProducts(35).filter(e => !e.delete_hidden)
    this.listProductApp = localProducts.map(item => {
      if (item[this.id_branch] && item[this.id_branch].tiktoks && item[this.id_branch].tiktoks.length) { //filter những sp tiktok thuộc id tiktok đã chọn
        item[this.id_branch].tiktoks = [...item[this.id_branch].tiktoks.filter(prod => prod.id_shop == id)];
      }
      return { ...item, ...this.vhQuerySales.getUnit_byRatio(item.units, 1) };
    });
    this.listSubProductApp = this.listProductApp;
    this.listSubProduct = this.cloneDeepProducts();
    this.search(this.searchValue);
  }
  trackByFn(index, item) {
    return index;
  }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/update-stock']);
  }
  //search tên sản phẩm trong app viethas
  search(value: string) {
    let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase());
    this.listProductApp = this.vhAlgorithm.searchList(val, [...this.listSubProductApp], ['name']);
  }
  ngAfterViewChecked() {
    if (document.querySelector("#by-product-app-stock") && document.querySelector(".ant-table-thead") && document.querySelector(".by-product-app-stock-header")) {
      this.heightScroll = document.querySelector("#by-product-app-stock").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".by-product-app-stock-header").clientHeight - 90 + "px";
    }
    this.cdRef.detectChanges();
  }
  /**
   * Show/hide sản phẩm con liên kết với sp của app
   * @param productApp
   */
  renderProductTiktoks(productApp) {
    if (!productApp.expand && !productApp.loaded) {
      let productTiktok = this.vhEcommerce.getlocalProductListTiktok(this.infoShop['_id']);
      if (productApp[this.id_branch] && productApp[this.id_branch].tiktoks && productApp[this.id_branch].tiktoks.length) {
        for (let indexProd in productApp[this.id_branch].tiktoks) {
          let prodTiktok = productTiktok.find(item => item.id == productApp[this.id_branch].tiktoks[indexProd].id);
          if (prodTiktok && productApp[this.id_branch].tiktoks[indexProd].skus.length) {
            for (let index in productApp[this.id_branch].tiktoks[indexProd].skus) {
              let findModel = prodTiktok.skus.find(item => item.id == productApp[this.id_branch].tiktoks[indexProd].skus[index].id);
              if (findModel) productApp[this.id_branch].tiktoks[indexProd].skus[index] = { ...findModel };
            }
            productApp[this.id_branch].tiktoks[indexProd] = { ...prodTiktok, ...productApp[this.id_branch].tiktoks[indexProd], update_time: new Date(+prodTiktok.update_time) };
          } else if (prodTiktok && !productApp[this.id_branch].tiktoks[indexProd].skus.length)
            productApp[this.id_branch].tiktoks[indexProd] = { ...prodTiktok, ...productApp[this.id_branch].tiktoks[indexProd], update_time: new Date(+prodTiktok.update_time) };
        }
        productApp.loaded = true;
      }
    }
    productApp.expand = !productApp.expand;
  }

  /**Clone mảng subproduct trong những sp được show ra */
  cloneDeepProducts(subProducts?) {
    let products = [];
    products = (subProducts || this.listProduct).map(item => {
      item.expandLoading = false;
      item.expandModal = false;
      item.isChecked = false;
      if (item.skus && item.skus.length) {
        item.skus = item.skus.map(data => {
          data.isChecked = false;
          return data;
        });
      }
      return item;
    });
    return [...products];
  }
  /**Kiểm tra input nhập vào của stock */
  checkStock(product, model, event) {
    if (typeof event == 'string')
      model ? delete model.newQuantity : delete product.newQuantity;
    else {
      if (model) model.newQuantity = event;
      else product.newQuantity = event;
    }
  }
  /**Cập nhật stock */
  updateQuantity(product, sku, id_product) {
    if (sku) sku.loading = true;
    else product.loading = true;
    const json = this.renderFormUpdate(sku);

    this.vhEcommerce.updateStockTiktok(
      this.infoShop['shop_cipher'],
      this.infoShop['app_key'],
      this.infoShop['access_token'],
      product.id.toString(),
      json,
      this.infoShop['_id']
    ).then((res: any) => {
      if (res.id === product.id) {
        sku['dateUpdateStock'] = new Date().toISOString();
        // this.updateDateForDatabase(product, sku, id_product).then((result) => {
        //   if (result) {
        //     sku.loading = false;
        //     sku.inventory[0].quantity = sku.newQuantity;
        //     sku.newQuantity = null;
        //     this.vhComponent.alertMessageDesktop("success", this.lang.translate("Update successfully"));
        //   }
        //   else this.vhComponent.alertMessageDesktop("error", this.lang.translate("Update failed"));
        // });
        this.vhEcommerce.updateMergeTiktokProduct_skus(id_product, product['id'], sku['id'],
          { dateUpdateStock: sku['dateUpdateStock'] }, this.infoShop['_id'], this.id_branch).then((result) => {
            sku.loading = false;
            sku.inventory[0].quantity = sku.newQuantity;
            sku.newQuantity = null;
            this.vhComponent.alertMessageDesktop("success", this.lang.translate("Update successfully"));
          });
      }
      else {
        this.vhComponent.alertMessageDesktop("error", this.lang.translate("Update failed"));
      }
    }).finally(() => {
      if (sku) sku.loading = false;
      else product.loading = false;
    });
  }
  /**
   * Cập nhật vào db ngày cập nhật sp
   */
  updateDateForDatabase(product, sku, id_product) {
    return new Promise(resolve => {
      if (sku) {
        sku['dateUpdateStock'] = new Date().toISOString();
        this.vhEcommerce.updateMergeTiktokProduct_skus(id_product, product['id'], sku['id'],
          { dateUpdateStock: sku['dateUpdateStock'] }, this.infoShop['_id'], this.id_branch).then(res => { resolve(res); });
      }
    });
  }

  /**chuyển data theo đúng định dạng update */
  renderFormUpdate(sku) {
    return {
      "skus": [
        {
          "id": sku.id,
          "inventory": [
            {
              "warehouse_id": sku.inventory[0].warehouse_id,
              "quantity": sku.newQuantity
            }
          ]
        }
      ]
    };
  }
}
